.cardsWrapper {
  display: flex;
  gap: 15px;
  margin-top: 50px;
  flex-wrap: wrap;       
  justify-content: center;
}

.card {
  background: #ffffffcc;
  backdrop-filter: blur(12px);
  border-radius: 22px;
  padding: 28px;
  width: 200px;
  height: 200px;
  box-shadow: 0 25px 55px rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* ------------ TEXT ------------ */

.title {
  font-size: 20px;
}

.desc {
  font-size: 15px;
}

