/* ------------ STAT CARD ------------ */

.statNumber {
  font-size: 26px;
}

.statLabel {
  font-size: 15px;
}

.statIcon {
  font-size: 26px;
  margin-bottom: 10px;
}

/* ------------ PROFILE IMAGES ------------ */

.peopleRow {
  display: flex;
  gap: -10px;
  margin-top: 15px;
}

.personImg {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  border: 2px solid #fff;
}

/* ------------ QUOTE ------------ */

.quote {
  font-size: 15px;
}

.quoteName {
  font-size: 14px;
  margin-top: 6px;
}


/* Mobile (max-width: 600px) */
@media (max-width: 600px) {

  .cardsWrapper {
    gap: 16px;
  }

  .card {
    width: 100%;         
    height: auto;        
    padding: 22px;
  }

  .title {
    font-size: 18px;
  }

  .desc {
    font-size: 14px;
  }

  .statNumber {
    font-size: 22px;
  }

  .personImg {
    width: 36px;
    height: 36px;
  }

  .quote {
    font-size: 14px;
  }
}

/* ðŸ“± Tablet (600pxâ€“900px) */
@media (min-width: 600px) and (max-width: 900px) {
  .card {
    width: 45%;          
  }
}
